import React, {Component} from 'react'
import {connect} from 'react-redux'
import {withRouter} from 'react-router'

import '../../components/Header/Nomer.css'
import {getNavigation} from '../../reducers/Navigation/reducer';

import {ListLinkItem} from './ListLinkItem'
import DropdownItems from './DropdownItems';
import Nomer from './Nomer';


class NavBar extends Component {
    render() {
        const {navigation} = this.props

        return (
            <React.Fragment>
                <button
                    className="navbar-toggler"
                    type="button"
                    data-toggle="collapse"
                    data-target="#navbarText"
                    aria-controls="navbarText"
                    aria-expanded="false"
                    aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"/>
                </button>
                <div className='collapse navbar-collapse' id="navbarText">
                    <ul className='navbar-nav mx-auto'>
                        {navigation.map((val, i) => {
                            if (val.isDropdown) {
                                return (<DropdownItems key={i} togleName={val.name} items={val.DropdownItems}/>)
                            } else {
                                return (
                                    <ListLinkItem key={i} to={val.to} name={val.name}></ListLinkItem>
                                )
                            }
                        })}
                    </ul>
                    <Nomer/>
                </div>
            </React.Fragment>
        )
    }
}

const mapStateToProps = (state, ownProps) => ({navigation: getNavigation(state)})

export default withRouter(connect(mapStateToProps)(NavBar))
