import React from 'react'
import GroupOfStars from './GroupOfStarts';
import PropTypes from 'prop-types'

import './Mark.css'

const Mark = (props) => (
    <div className='d-flex pt-2'>
        <span className="font_6 flex-grow-1">{props.name}</span>
        <GroupOfStars numerTrue={props.mark} numerAll={5}/>
    </div>
)

export default Mark

Mark.propType = {
    name: PropTypes.string.isRequired,
    mark: PropTypes.number.isRequired
}