import React, {Component} from 'react'
import classNames from 'classnames'

import Background from './Background';
import './ServiceView.css'
import {platform} from '../../utils/platform';
import ServiceButton from './ServiceButton';

export default class ServiceView extends Component {
    render() {
        const isMobile = platform()
        const {info} = this.props
        return (
            <div className='container mt-4'>
                <div
                    className={classNames('row', {
                    'position-relative ': !isMobile
                })}>
                    <Background/>
                    <div
                        className={classNames('row', {
                        'Img-wrap p-3': !isMobile
                    })}>
                        {info.map((val, i) => <ServiceButton isMobile={isMobile} isNextRow={ i>2} key={i} to={val.to} alt={val.alt}/>)}
                    </div>
                </div>
            </div>
        )
    }
}
