import React from 'react'
import { Helmet } from "react-helmet";

const UnkownPage = () => (
    <div className='container mt-5'>
        <div className='row'>
            <div className='h2'>404 - not found</div>
        </div>
        <div className='row'>
            <div className='col-sm-12 h4'>Здравствуй,
                <strong>Дорогой друг</strong>
            </div>
            <div className='h4'>
                Cтраница, на которую ты перешёл, не найдена или ещё не сотворена ^_^</div>
        </div>
        <br/>
        <h5 className='float-right'>Обещали скоро сделать, подожди ; )</h5>
        <br/>
        <Helmet>
            <meta name="prerender-status-code" content="404"></meta>
        </Helmet>
    </div>
)
export default UnkownPage