import {FETCH_HASHTAGINFO_FAILED, FETCH_HASHTAGINFO_SUCCESS, HASHTAG_UNKOWN} from './actionTypes'

// 8624126931 - cleantonn_detailing  cleantonn_детейлинг 8625214852 -
// cleantonn_restore 1583484457 - cleantonn cleantonn_блог

export function fetchdata(hashtag, sliceCount = 9) {
    return async(dispatch, getstate) => {
        dispatch({type: 'FETCHING_HASHTAGINFO'})
        if (hashtag) {
            try {
                const data = await fetch(`https://www.instagram.com/explore/tags/${encodeURIComponent(hashtag)}/?__a=1`).then((response) => {
                    if (response.ok) {
                        return response;
                    }
                    throw new Error('Network response was not ok.');
                })
                    .then(res => res.json())
                    .then(res => res.graphql.hashtag.edge_hashtag_to_media.edges)
                    .then(res => res.map((val) => val.node))
                    .then(res => res.filter((val, i) => + val.owner.id === 8624126931 || + val.owner.id === 8625214852 || + val.owner.id === 1583484457))
                    .then(res => res.sort((x, y) => x.taken_at_timestamp - y.taken_at_timestamp))
                    .then(res => res.slice(0, sliceCount))

                dispatch({type: FETCH_HASHTAGINFO_SUCCESS, hashtagData: data, hashtagName: hashtag})
            } catch (e) {
                console.error(e)
                dispatch({type: FETCH_HASHTAGINFO_FAILED, error: e})
            }
        } else {
            dispatch({type: HASHTAG_UNKOWN})
        }
    }
}

