import React from 'react'
import Marks from './Zvezdi/Marks';
import './Info.css'
const Info = (props) => (
    <div className={'col-sm-9 order-' + ~~ props.left}>
        <div className='p-3 h-100 Info'>
            <h5 className='mt-0'>
                {props.title}</h5>
            <div className='pl-1'>
                {props.izStars
                    ? <Marks values={props.values}/>
                    : <p className='font_7'>{props.info}</p>}
            </div>
        </div>
    </div>
)
export default Info