import React from 'react'
import But from './But/But'

const Buts = (props) => (
    <div className='row '>
        <div className='container mt-3'>
            <div className='borderr'>
                <ul className='font_7 m-2'>
                    {props
                        .advantages
                        .map((val, i) => (<But key={i} main={val.main} but={val.but}/>))}
                </ul>
            </div>
        </div>
    </div>
)
export default Buts
