export default class Slogan extends Component {
    render() {
        const {data} = this.props
        return (
            <div className='col-sm '>

                <BlackBlock>
                    {data.map((val, i) => {
                        /*
                        if (val.oneString) {
                            return <Nadpis key={i} obj={val} isCombo/>
                        } else */
                        return <Nadpis
                            isCombo={val.oneString}
                            isBlue={val.isBlue}
                            key={i}
                            obj={val.oneString
                            ? val
                            : null}
                            text={val.text}/>
                    })}
                </BlackBlock>
            </div>
        )
    }
}

Slogan.propType = {
    data: PropTypes
        .arrayOf(PropTypes.shape({isBlue: PropTypes.bool, text: PropTypes.string.isRequired}).isRequired,)
        .isRequired
}