const ImgSlide = (props) => (
    <div className='px-lg-3 py-lg-2'>
        <img src={props.itemLink} alt="Second slide"/>
    </div>
)

export default class InstagramCarousel extends Component {

    render() {
        const {photos} = this.props

        const settings = {
            infinite: true,
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 3,
            autoplaySpeed: 5000,
            autoplay: true,
            className: 'Product',
            initialSlide: -1,
            pauseOnHover: true,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                        infinite: true,
                        dots: true
                    }
                }, {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                        initialSlide: 2
                    }
                }, {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        }

        if (!photos) 
            return <div/>

        return (
            <div className='col-sm-12'>
                <Slider {...settings}>
                    {photos.map((val, i) => <ImgSlide key={i} itemLink={val.display_url}/>)}
                </Slider>
            </div>
        )

    }
}

InstagramCarousel.propTypes= {
    photos: PropTypes.array
}