import React, {Component} from 'react'
import {connect} from 'react-redux'

import InstagramCarousel from '../../components/Instagram/InstagramCarousel';
import InstaHead from '../../components/Instagram/InstaHead';

import {getInstaPhotos} from '../../reducers/Instagram/reducer';
import * as instagramActions from '../../reducers/Instagram/action'

class InstagramFeed extends Component {

    componentDidMount() {
        this
            .props
            .fetchHashtagPhoto(this.props.hashTag)
    }

    componentDidUpdate(prevProps) {
        if (this.props.hashTag !== prevProps.hashTag) {
            this
                .props
                .fetchHashtagPhoto(this.props.hashTag)
        }
    }

    render() {
        const {username, insta} = this.props
        return (
            <div className='row mt-3'>
                {insta.canShow
                    ? <InstaHead username={username}></InstaHead>
                    : null}

                <InstagramCarousel photos={insta.photos}/>
            </div>
        )

    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        insta: getInstaPhotos(state, ownProps.hashTag)
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        fetchHashtagPhoto: (hashTag) => dispatch(instagramActions.fetchdata(hashTag))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(InstagramFeed)