import React, {Component} from 'react'
import OrderView from '../OrderView/OrderView';
import Info from './Info';

export default class Usluga extends Component {
    render() {
        const {value, left} = this.props
        const {title, info, price, izStars, values} = value
        return (
            <div className='row mt-3  '>
                <Info
                    title={title}
                    izStars={izStars}
                    values={izStars
                    ? values
                    : null}
                    info={info}
                    left={left}/>
                <OrderView order={~~ !left} price={price}/>
            </div>

        )
    }
}