import React, {Component} from 'react'
import PropTypes from 'prop-types'
import './Rewiews.css'

const RewiewTitle = (props) => (
    <h5>
        <em>
            {props.auto}
            <span className='kosoy'>|</span>
            {props.workType}
        </em>
    </h5>
)
const RewiewWho = (props) => (
    <p className='who w-100 h-auto'>
        <a target='_blank' rel="noopener noreferrer" href={props.link}>
            <strong>
                <em>
                    <u >{props.who}</u>
                </em>
            </strong>
        </a>
    </p>
)

export default class RewiewContent extends Component {
    render() {
        const {link, who, rewiew, auto, workType} = this.props.value

        return (
            <div className='media-body rewiew font_8'>
                <RewiewTitle auto={auto} workType={workType}/>
                <p className='mt-2 '>{rewiew}</p>
                <RewiewWho link={link} who={who}/>
            </div>
        )
    }
}
RewiewContent.propType = {
    value: PropTypes
        .shape({link: PropTypes.string.isRequired, who: PropTypes.string.isRequired, rewiew: PropTypes.string.isRequired, auto: PropTypes.string.isRequired, workType: PropTypes.string.isRequired})
        .isRequired
}