import React, {Component} from 'react'
/*
import classNames from 'classnames'
import {platform} from '../../utils/platform'
*/
const NadpisBlue = (props) => <div className='h2 font_2 color2 mb-0'>
    {props.text}</div>

const NadpisWhite = (props) => <div className='h2 font_2 text-white mb-0'>
    {props.text}
</div>
/*
class NadpisCombo1 extends Component {
    render() {
        const {firstBlue, white, blue, needSpace} = this.props.obj
        return (
            <div className='h2 d-flex text-white font_2 mb-0'>
                <div
                    className={classNames('color2', {
                    'order-1': firstBlue
                }, {
                    'order-2': !needSpace && !firstBlue
                }, {
                    'order-3': needSpace && !firstBlue
                })}>{blue}</div>
                {needSpace
                    ? <div className='order-2'>&nbsp;</div>
                    : null}
                <div
                    className={classNames('text-white', {
                    'order-1': !firstBlue
                }, {
                    'order-2': !needSpace && firstBlue
                }, {
                    'order-3': needSpace && firstBlue
                })}>{white}</div>
            </div>
        )
    }
}*/
class NadpisCombo extends Component {
    render() {
        const { white, blue, needSpace} = this.props.obj
        //const isMobile = platform();
        return (
            <div className='h2 text-white d-flex font_2 mb-0'>
                <div>{white}</div>
                {needSpace
                    ? <div >&nbsp;</div>
                    : null}
                <span className='color2 '>{blue}</span>
            </div>
        )
    }
}
/*
                className={classNames('h2 d-flex flex-column text-white font_2 mb-0', {
                'flex-lg-row-reverse': firstBlue,
                'flex-lg-row': !firstBlue
            }, {
                'flex-column-reverse': firstBlue,
                'flex-column': !firstBlue
            })}*/
export default class Nadpis extends Component {

    render() {
        const {isCombo, obj, text, isBlue} = this.props
        if (isCombo) 
            return (<NadpisCombo obj={obj}/>)
        else if (isBlue && text) {
            return (<NadpisBlue text={text}/>)
        } else {
            return (<NadpisWhite text={text}/>)
        }
    }

}