import React, {Component} from 'react'
import {connect} from 'react-redux'
import './ServiceMain.css'

import {getServiceInfo} from '../../reducers/Navigation/reducer'
import ServiceView from '../../components/ServiceMain/ServiceView';

export class ServiceMain extends Component {
    render() {
        const {info} = this.props
        return <ServiceView info={info}/>
    }
}
const mapStateToProps = (state, ownProps) => ({info: getServiceInfo(state)})

export default connect(mapStateToProps)(ServiceMain)