import React, {Component} from 'react'
import {connect} from 'react-redux'
import {getService} from '../../reducers/Service/reducer'
import UnkownPage from '../404';

import ServiceView from '../../components/Service/ServiceView';
import InstagramFeed from '../Instagram/InstagramFeed';
import ServiceHeader from '../../components/Service/ServiceHeader/ServiceHeader';

export class ServicePage extends Component {

    render() {
        if (this.props.service) {
            const {
                name,
                slogan,
                items,
                isCustom,
                hashTag,
                username,
                ownerId
            } = this.props.service
            return (
                <div className='container mt-4'>
                    <ServiceHeader name={name} slogan={slogan}/>
                    <ServiceView isCustom={isCustom} items={items}/>
                    <InstagramFeed hashTag={hashTag} ownerId={ownerId} username={username}/>
                </div>
            )
        } else {
            return <UnkownPage/>
        }
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        service: getService(ownProps.type, state)
    }
}

export default connect(mapStateToProps)(ServicePage)