const CustomOrder = () => (
    <React.Fragment>
        <Nadpis text='ЖМИ СЮДА И ЗАКАЖИ'/>
        <Nadpis isBlue text='Прямо сейчас'/>
    </React.Fragment>
)
const Order = (props) => (
    <React.Fragment>
        <Nadpis isBlue text={'от ' + props.price + ' руб.'}/>
        <Nadpis text='ЗАКАЗАТЬ'/>
    </React.Fragment>
)

const OrderView = (props) => {
    return (
        <div className={'col-sm-3 order-' + props.order}>
            <BlackBlock>
                <a href='tel:+78412306806'>
                    {props.isCustom
                        ? <CustomOrder/>
                        : <Order price={props.price}/>}
                </a>
            </BlackBlock>
        </div>
    )

}
OrderView.propType = {
    order: PropTypes.number.isRequired,
    isCustom: PropTypes.bool.isRequired,
    price: PropTypes.string
}
export default OrderView
