import React, {Component} from 'react'
import Photo from '../../components/Rewiew/RewiewHead/Photo';
import Lozung from '../../components/Rewiew/RewiewHead/Lozung';
import About from '../../components/Rewiew/RewiewHead/About';
import Rewiews from '../../components/Rewiew/RewiewBody/RewiewsBody'
import {connect} from 'react-redux'
import {getRewiews} from '../../reducers/Rewiews/reducer';

class RewiewView extends Component {
    render() {
        const {rewiews} = this.props
        return (
            <div className='container mt-4'>
                <div className='row '>
                    <Photo/>
                    <Lozung/>
                    <About/>
                </div>
                <Rewiews rewiews={rewiews}/>

            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => ({rewiews: getRewiews(state)})

export default connect(mapStateToProps)(RewiewView)