import React, {Component} from 'react'
import Usluga from './Usluga/Usluga';

export default class ServiceBody extends Component {
    render() {
        const {items} = this.props
        return (
            <React.Fragment>
                {items.map((val, i) => <Usluga value={val} key={i}/>)}
            </React.Fragment>
        )
    }
}
