import React, {Component} from 'react'
import Rewiew from './Rewiew'

export default class Rewiews extends Component {
    render() {
        const {rewiews} = this.props
        return (
            <React.Fragment>

                <div className='row mt-4'>
                    {rewiews.map((val, i, ar) => <Rewiew value={val} key={i}/>)}
                </div>

            </React.Fragment>

        )
    }
}
