import React, {Component} from 'react'
import Buts from '../../components/Advantages/Buts'
import AdvantagesHeader from '../../components/Advantages/AdvantagesHeader';
import {connect} from 'react-redux'
import {getAdvantages} from '../../reducers/Advantages/reducer'

export class Advantages extends Component {
    render() {
        const {advantages} = this.props
        return (
            <div className='container mt-4'>
            
                <AdvantagesHeader/>
                <Buts advantages={advantages}/>
            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => ({advantages: getAdvantages(state)})

export default connect(mapStateToProps)(Advantages)