import React from 'react'
import classNames from 'classnames'
import {NavLink} from 'react-router-dom'
import './ServiceButton.css'

const ServiceButton = (props) => (
    <div className={classNames('col-12  mt-3', {'col-lg-4': !props.isNextRow }, {'col-lg-3': props.isNextRow})}>

        <NavLink
            className='text-white navigation-link'
            style={{
                whiteSpace: 'nowrap'
            }}
            to={props.to}>
            <h1 
                className={classNames('navigation-link mb-0 p-2', {
                'h4 mx-3': props.isMobile
            }, {
                'h4 border-3px-white': !props.isMobile
            })}>{props.alt}</h1>
        </NavLink>

    </div>
)
export default ServiceButton