import React, {Component} from 'react'
import PropTypes from 'prop-types'
import Zvezda from './Zvezda';

export default class GroupOfStars extends Component {
    //TODO
    render() {
        const {numerTrue, numerAll} = this.props
        let a = 'a'.repeat(numerTrue) + 'b'.repeat(numerAll - numerTrue)
        let b = a.split('')
        return (
            <div className='d-flex ml-auto allign-items-right float-right'>
                {b.map((val, i) => {
                    if (val === 'a') {
                        return (< Zvezda key ={i} opacity = '1' />)
                    }else{
                        return (< Zvezda key={i} opacity='0.15' />)
                    }
                })}
            </div>
        )
    }
}
GroupOfStars.propTypes = {
    numerTrue: PropTypes.number.isRequired,
    numerAll: PropTypes.number.isRequired
}