import React from 'react'
import {Route, Switch} from 'react-router'
import Header from "../containers/Header/Head";
import Rewiews from "../containers/Rewiew/RewiewView";
import Advantages from "../containers/Advantages/Advantages";
//import Offers from "../containers/Offers";
//import Service from "./-service";
import Contact from '../containers/Contact/MainView'
import UnkownPage from "../containers/404";
import Footer from '../components/Footer/Footer';
import ServiceMain from '../containers/ServiceMain';
import MetaTags from '../containers/MetaTags';
import ServicePage from "../containers/Service/index";

const routes = (
    <div>
        <Header/>
        <MetaTags/>
        <main>
            <Switch>
                <Route exact path='/' component={ServiceMain}/>
                <Route path='/rewiews' component={Rewiews}/>
                <Route path='/advantages' component={Advantages}/> 
                {/*<Route path='/offers' component={Offers}/>*/}
                {/*<Route path='/service' component={Service}/>*/}
                <Route path='/contact' component={Contact}/>
                <Route
                    path='/:type'
                    component={(props) => <ServicePage type={props.match.params.type}/>}/>
                <Route component={UnkownPage}/>
            </Switch>
        </main>
        <div className='line mt-1'></div>
        <Footer/>
    </div>
)
export default routes