import React from 'react'

import qotes from '../../../images/qotes.jpg'
import RewiewContent from './RewiewContent';

const Rewiew = (props) => (
    <div className='col-lg-6 media p-3'>
        <img src={qotes} className='align-self-start mr-3' alt=''/>
        <RewiewContent value={props.value}/>
    </div>
)

export default Rewiew