import React from 'react'
import './About.css'
const About = () => (
    <div className='col-sm d-flex align-items-center About mx-3'>
        <div className='font_7 m-3'>
            <div>
                Приятно осознавать, что ты двигаешься к цели каждый день, маленькими шажками,
                единичными действиями в правильном направлении!
            </div>
            <br/>
            <div>
                Нам важно не количество клиентов, а количество их улыбок! Каждая из которых
                выражает приятное удивление.
            </div>
            <br/>
            <div>
                Спасибо вам за то, что вы цените чистоту! Ждем ваши улыбки в наших центрах)
            </div>
        </div>
    </div>
)
export default About
