import React, {Component} from 'react'
import {NavLink} from 'react-router-dom'
import {DropdownItemLink} from './DropdownItemLink'

import PropTypes from 'prop-types'
import './NavBar.css'

export default class DropdownItems extends Component {
    render() {
        const {items, togleName} = this.props
        return (
            <li className='nav-item dropdown mx-auto'>
                <NavLink
                    className=" font_7 nav-link text-center dropdown-toggle"
                    id="navbarDropdown"
                    role='button'
                    data-toggle="dropdown"
                    to='#'                   
                    aria-haspopup="true"
                    activeClassName='active'
                    aria-expanded="true">
                    {togleName}
                </NavLink>
                <div className='dropdown-menu ' aria-labelledby="navbarDropdown">
                    {items.map((val, i) => (
                        <DropdownItemLink key={i} to={val.to} name={val.name}></DropdownItemLink>
                    ))}

                </div>
            </li>
        )
    }
}
DropdownItems.propTypes = {
    togleName: PropTypes.string.isRequired,
    items: PropTypes
        .arrayOf(PropTypes.shape({name: PropTypes.string.isRequired, to: PropTypes.string.isRequired}).isRequired,)
        .isRequired
}
