import React from 'react';
import {render} from 'react-dom'
//import {render} from 'react-snapshot'
import {createStore, applyMiddleware, compose} from 'redux'
import Logger from 'redux-logger'
import {Provider} from 'react-redux'
import thunk from 'redux-thunk'
import {ConnectedRouter, routerMiddleware} from 'connected-react-router';

import {createBrowserHistory} from 'history';
//import {createHashHistory} from 'history';

import createRootReducer from './reducers'

import routes from './routes/index'
import './fonts/fonts.css'

import * as serviceWorker from './serviceWorker';

const history = createBrowserHistory({hashType: 'hashbang'})
//const history = createHashHistory({ hashType: 'hashbang'})

const composeEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
let middleware = [thunk, routerMiddleware(history)]

if (process.env.NODE_ENV !== 'production') {
    middleware = [
        ...middleware,
        Logger
    ]
}

const store = createStore(createRootReducer(history), composeEnhancer(applyMiddleware(...middleware)))

const rootElement = document.getElementById('root');

// if (rootElement.hasChildNodes()) {
//     hydrate(
//         <Provider store={store}>
//         <ConnectedRouter history={history}>
//             {routes}
//         </ConnectedRouter>
//     </Provider>, rootElement);
// } else {
    render(
        <Provider store={store}>
        <ConnectedRouter history={history}>

            {routes}

        </ConnectedRouter>
    </Provider>, rootElement);
// }

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls. Learn
// more about service workers: http://bit.ly/CRA-PWA
serviceWorker.register()
