import React from 'react'
import PropTypes from 'prop-types'

const But = (props) => (
    <li >
        <p>
            <strong>{props.main}</strong>
            <span>&nbsp;</span>{props.but}
        </p>
    </li>
)

But.propType = {
    
    but: PropTypes.string.isRequired,
    main: PropTypes.string.isRequired
}

export default But
