import React, {Component} from 'react'
import './Footer.css'
import {withRouter} from 'react-router'
import {NavLink} from 'react-router-dom'

import VkIcon from './VkIcon'
import PlaceholderIcon from './PlaceholderIcon';
import CallIcon from './CallIcon';
import Link from './Link';
import InstaIcon from './InstaIcon';

import './Footer.css'
//import Instagram from './Instagram/Instagram';

class Footer extends Component {
    render() {
        return (
            <footer className='container-fluid mx-lg-5'>
                <div className='row'>
                    <div className='col-sm-3 no-gutters mt-3'>
                        <NavLink to='/contact' className='d-flex'>
                            <PlaceholderIcon/>
                            <div className='col-auto pl-2'>
                                <span className='align-middle'>пр-д Байдукова 3, Пенза</span>
                            </div>
                        </NavLink>
                    </div>
                    
                    <div className='col-sm-3 no-gutters mt-3'>
                        <Link href='https://vk.com/cleantonn' newTab className='d-flex'>
                            <VkIcon/>
                            <div className='col-auto pl-2 pt-1'>
                                <span className='align-middle font_7'>cleantonn</span>
                            </div>
                        </Link>
                    </div>

                    <div className='col-sm-3 no-gutters mt-3'>
                        <Link href='https://instagram.com/cleantonn' newTab className='d-flex'>
                            <InstaIcon/>
                            <div className='col-auto pl-2 pt-1'>
                                <span className='align-middle font_7'>@cleantonn</span>
                            </div>
                        </Link>
                    </div>
                    <div className='col-sm-3 no-gutters mt-3'>
                        <Link href='tel:+78412306806' className='d-flex'>
                            <CallIcon />
                            <div className='col-auto pl-2'>
                                <span className='align-middle color-grey font_2 '>+7(8412)<nobr className='font_5' style={{fontWeight:'bold'}}>306-806</nobr>
                                </span>
                            </div>
                        </Link>
                    </div>
                </div>
                <div className='row no-gutters mt-4'>
                    <div >©2018 Центр по уходу за автомобилем «Чистая тонна»</div>
                    {/*2015*/}
                </div>

                {/* <div style={{ height: 15, width: 15 }}>
                        <VkIcon></VkIcon>
                    </div> */}

            </footer>
        )
    }
}

export default withRouter(Footer)