import React, {Component} from 'react'
import {Helmet} from "react-helmet";
import {withRouter} from 'react-router'
import {getSeoData} from '../reducers/Navigation/reducer';
import {connect} from 'react-redux'

class MetaTags extends Component {
    render() {
        const {navigationData} = this.props
        if (navigationData) {
            const { name, description, keywords, canonicalUrl} = navigationData
            return (
                <Helmet
                    titleTemplate='%s | Центр реставрации и ухода за автомобилем "Чистая тонна"!'> {/* Центр по уходу за автомобилем &#34;Чистая тонна&#34; - Cleantonn в Пензе'>*/}
                    {name
                        ? <title>{name}</title>
                        : null}
                    {description
                        ? <meta name='description' content={description}/>
                        : null}
                    {keywords
                        ? <meta name='keywords' content={keywords}/>
                        : null}
                    {canonicalUrl
                        ? <link rel='canonical' href={canonicalUrl}/>
                        : null}
                </Helmet>
            )
        }else{
            return <div></div>
        }
    }
}

const mapStateToProps = (state, ownProps) => ({
    navigationData: getSeoData(ownProps.location.pathname, state)
})

export default withRouter(connect(mapStateToProps)(MetaTags))
